    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

/**** CSS variables *****/
:root{
    --main-color-1: rgba(0,174,255,0.5);
    --main-color-2: rgb(0,67,255);
    --main-color-3: rgb(0,41,187,0.5);
    --main-color-4: rgb(0,15,92);
    --text-color: rgb(5, 35, 80);

}

#app{  
    height: 100vh;
    width: 100vw;
    padding: 2em;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center 35%;
    color: wheat;
    display: grid;
    grid-template-rows: 50vh 30vh;
    grid-template-columns: 1fr;
    grid-template-areas: "weatherInfo weatherInfo"
                "weatherPrevision weatherPrevision";
    gap: 2em;
    justify-content: center;
    
}

.Init
{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--main-color-2);
    font-size: 1.5em;
}
button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--main-color-1);
    color: var(--main-color-4);
    width: 2em;
    height: 2em;

    border: 1px dotted var(--main-color-3)
}

form{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:0.3em
}
input{
    height: 2em;
    background-color: var(--main-color-1);
    color: black;
    border: 1px dotted var(--main-color-3)
}
input::placeholder
{
    text-align: center;
    color: var(--main-color-4);
}
.material-symbols-outlined.Icon-description-weather {
    font-size: 2em !important ;
}
.material-symbols-outlined
{
    font-size: 1.5em!important;
}


.title{
    font-size: 2em;
    grid-area: title;
    text-align: center;

}

.Weather-info{
    padding: 1em;
    grid-area: weatherInfo;

    border-radius: 10px;
    display: grid;
    justify-content: center;
    grid-row: 1/2;
       
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 0.10fr 2fr;
    grid-template-areas: 
    "title title "
    "weatherLeft weatherRight "
     ;
    gap: 1em;
    font-size: 0.75em;

    background-color: var(--main-color-1);
    color: var(--text-color)
}

.Weather-info-left
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 5%;
    border-bottom: solid 3px var(--main-color-3);
    border-left: solid 2px var(--main-color-4);
    border-top: solid 2px var(--main-color-3);
    border-right: solid 2px var(--main-color-3); ;
    border-radius: 5px;
    font-size: 1.25em;
    grid-area: weatherLeft;
}

.Weather-info-right
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 5%;


    border-bottom: solid 3px var(--main-color-3);
    border-left: solid 2px var(--main-color-4);
    border-top: solid 2px var(--main-color-3);
    border-right: solid 2px var(--main-color-3); 
    border-radius: 5px;
    font-size: 1.25em;
    grid-area: weatherRight;

}
.weather-detail-right
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.25em;
    width: 80%;
}

.Weather-info-right span
{
    font-size: 2.25em !important;
    display: flex;
    justify-content:left;
    align-items:center;
} 

.weather-prevision
{
    grid-area: weatherPrevision;
    grid-row: 2/2;
    border-radius: 10px;
    
    display: grid;
    grid-template-rows: 15% 85%;
    grid-template-columns: repeat(4,1fr);
    grid-template-areas:
    "previsionType previsionType previsionType previsionType"
    "day1 day2 day3 day4 ";
    padding: 2em;
    gap: 0.5em;
    background-color: var(--main-color-1);
    color: var(--text-color);

}


.Prevision-type
{
    grid-area: previsionType;
    display: flex;
    align-items: center;

}
.btn-prevision-type{
    border-radius: 5px;
    width: 5em;
    font-size: 1em;
    padding: 1em;
    background-color: var(--main-color-1);
    color: var(--text-color);
}

.focus
{
    border: solid 2px var(--text-color);
}

.prevision-day
{
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
    align-items: center;
    font-size: 1em;
    gap: 1em;
    border-radius: 10px;
    
}


.day-1{grid-area: day1; border: 1px solid var(--text-color);}
.day-2{grid-area: day2; border: 1px solid var(--text-color);}
.day-3{grid-area: day3; border: 1px solid var(--text-color);}
.day-4{grid-area: day4; border: 1px solid var(--text-color);}

.prevision-description
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid black;
    border-top: 2px solid black;

    padding-bottom: 0.5em;
    padding-top: 0.5em;

    
    border-radius: 10px;

    
}

.Error
{
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
    font-size: 0.75em;
}
.Error.title{
    font-size: 2em;
}


@media (max-width:500px)
{
    #app{
       padding: 0.5em;
       gap: 0.5em;
       grid-template-rows: 60% 40%;
    }
    .Weather-info{
        grid-template-columns: repeat(2,minmax(150px,1fr));
    }
    .title
    {
        font-size: 1.5em;
    }
    input{
        height: 1.5em;
        width: 100%;
    }
    button{
    width: 1.5em;
    height: 1.5em;
    }
    form{
        margin: 0.25em;
        gap: 0.25em;
    }

    .material-symbols-outlined.Icon-description-weather {
        font-size: 2.5em !important ;
    }
    .material-symbols-outlined
    {
        font-size: 1em!important;
    }
    .Weather-info-left{
        font-size: 1em;
    }

    .Weather-info-right span
    {
        font-size: 1.75em !important;
        display: flex;
        justify-content:left;
        align-items:center;
    }
    
    .weather-detail-right
    {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.75em;
    font-size: 0.75em;
    width: 80%;
    }
    .weather-prevision{
        padding: 0;
        font-size: .75em;
    }
    .prevision-day  
    {

    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
    align-items: center;
    font-size: 1em;
    border: 1px solid black;
    border-radius: 10px;
    
    }
.prevision-description{
    border: none;
    max-height: 75px;
    padding-left: 0.2em;
    gap: 0.5em;
    display: flex;
    justify-content: center;
    align-items: center;
}


}





